.wrapper{display:flex;justify-content:center;align-items:center}.wrapper::before{content:"▾";color:var(--white);width:33px;font-size:28px;position:absolute;bottom:-12px;transform:scaleX(1.5) rotate(180deg);z-index:10000;pointer-events:none}.menu-items>div{padding-bottom:8px}.menu-items .menu-body{overflow-y:auto}.menu-items .menu-item{margin-left:0 !important;justify-content:start;width:100%;padding:8px 16px;border-radius:0}.menu-items .menu-item:hover{background-color:var(--secondary-003)}.menu-items .menu-item.separate{border-top-color:var(--secondary-012)}.menu-items .menu-item.subitem{font-weight:400;font-size:1.4rem}.header{position:relative;height:50px;margin-top:-8px;min-width:170px}.back-btn{width:50px;height:100%;position:absolute;background-color:rgba(0,0,0,0);cursor:pointer}.header-title{text-align:center;line-height:50px;font-weight:600;cursor:pointer}